.spliting-friend-pop-up-window {
  align-items: center;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  height: 1083px;
  overflow: hidden;
  width: 397px;
}

.group-1 {
  align-items: flex-start;
  display: flex;
  margin-left: 2.0px;
  min-width: 399px;
}

.overlap-group-20 {
  align-items: flex-end;
  background-color: #c4c4c4a6;
  display: flex;
  height: 35px;
  min-width: 397px;
  padding: 8px 23px;
}

.search-friend {
  background-color: transparent;
  border: 0;
  color: var(--black);
  font-family: var(--font-family-roboto);
  font-size: 15px;
  font-weight: 400;
  height: 18px;
  letter-spacing: 0;
  padding: 0;
  text-align: center;
  width: 92px;
}

.search-friend::placeholder {
  color: #00000099;
}

.overlap-group5-4 {
  align-items: flex-start;
  background-color: var(--white);
  display: flex;
  flex-direction: column;
  margin-left: 1.0px;
  margin-top: 17px;
  min-height: 979px;
  padding: 3px 0;
  width: 340px;
}

.overlap-group-19 {
  align-items: center;
  background-color: var(--white);
  border-radius: 10px;
  display: flex;
  height: 78px;
  margin-left: 3px;
  min-width: 334px;
  padding: 11px 17px;
  position: relative;
}

.friend-picture-3 {
  align-self: flex-start;
  height: 53px;
  width: 54px;
}

.user-name-2 {
  height: 14px;
  letter-spacing: 0;
  margin-bottom: 16.0px;
  margin-left: 23px;
  min-width: 60px;
  text-align: center;
}

.flex-row-4 {
  align-items: flex-start;
  display: flex;
  height: 24px;
  margin-left: 5.0px;
  margin-top: 12px;
  min-width: 242px;
  position: relative;
}

