:root { 
  --black: #000000;
  --black-2: #0000008f;
  --blue: #0803ff;
  --downriver: #091a55;
  --mist-gray: #c4c4c4;
  --mist-gray-2: #c4c4c459;
  --quick-silver: #a5a7ad;
  --star-dust: #a29b9b;
  --white: #ffffff;
 
  --font-size-l: 13px;
  --font-size-m: 12px;
  --font-size-xl: 14px;
  --font-size-xxl: 16px;
  --font-size-xxxl: 18px;
  --font-size-xxxxl: 20px;
  --font-size-xxxxxl: 25px;
 
  --font-family-roboto: "Roboto", Helvetica;
}
.roboto-bold-white-12px {
  color: var(--white);
  font-family: var(--font-family-roboto);
  font-size: var(--font-size-m);
  font-style: normal;
  font-weight: 700;
}

